<template>
    <div class="yu-home">
        <Hemera>
            <div class="yu-home__author">
                <div class="yu-home__author--name">谕℃</div>
                <div class="yu-home__author--bio">命运可以打破 但定数必须遵守</div>
            </div>
        </Hemera>
        <div class="yu-home__main">
            <Card v-if="articles" v-for="(v,i) in articles" :key="i">
                <Wrapper :img="v.wrapper" :height="wrapperHight"></Wrapper>
                <Gaea :data="v"></Gaea>
                <Nail :color="v.nail.color">{{v.nail.name}}</Nail>
            </Card>

            <PageButton></PageButton>
        </div>
        <SideMenu></SideMenu>
    </div>
</template>

<script>

    import Header from '@/components/Header/index'

    import Card from '@/components/Card/index'
    import Wrapper from '@/components/Wrapper/index'
    import Gaea from '@/components/Gaea/index'
    import Nail from '@/components/Nail/index'

    import Hemera from '@/components/Hemera/index'
    import PageButton from '@/components/PageButton/index'
    import SideMenu from '@/components/SideMenu/index'

    // import api from '@/http/api'
    import mock from '@/http/mock'

    export default {
        name: "Home",
        components: {
            Header,
            Card,
            Wrapper,
            Gaea,
            Nail,
            Hemera,
            PageButton,
            SideMenu
        },
        data(){
          return {
              articles: [],
              wrapperHight:"3rem"
          }
        },
        mounted() {
            if(this.$isMobile){
                this.wrapperHight = "2.5rem"
            }
            this.getArticles()
        },
        methods:{
            getArticles(){
                this.articles = mock.articles.data.list
            }
        }
    }
</script>