<template>
    <div class="yu-wrapper" v-show="value">
        <img :src="value" class="yu-wrapper__img" alt="lhlyu"
             :style="{
                height:height,
                width:width
             }">
    </div>
</template>

<script>

    import util from '@/util/util'

    export default {
        name: "Wrapper",
        props:{
            img:{
               type: String,
                validator(val){},
                default: null
            },
            height:{
                type: String,
                default: "100%"
            },
            width:{
                type: String,
                default: "100%"
            }
        },
        data(){
          return {
              value:null
          }
        },
        async mounted(){
            this.loadImg()
        },
        methods:{
            loadImg(){
                if(this.img){
                    let _this = this
                    util.loadImageAsync(this.img).then(v =>{
                        _this.value = v.src
                    })
                }
            }
        }
    }
</script>