<template>
    <div class="yu-hemera" ref="hemera">
        <Snow v-if="!noImage"></Snow>
        <img v-if="!noImage" class="yu-hemera--img" :src="value" alt="lhlyu">
        <slot v-if="!noImage"></slot>
    </div>
</template>

<script>
    import Snow from '../Snow/index'

    import util from '@/util/util'

    export default {
        name: "Hemera",
        components:{
            Snow
        },
        props:{
            img:{
                type: String,
                validator(val){},
            },
            noImage:{
                type: Boolean,
                default: false
            }
        },
        data(){
            return {
                value:""
            }
        },
        mounted(){
            if(this.noImage){
                this.$refs.hemera.style.visibility = 'hidden'
                this.$refs.hemera.style.height = '0.75rem'
            }
            this.loadImg()
        },
        methods:{
            loadImg(){
                let _this = this
                util.loadImageAsync(this.img).then(v =>{
                    _this.value = v.src
                })
            }
        }
    }
</script>