<template>
    <div class="yu-sidemenu" v-show="show">
        <div class="items">
            <div class="item"><i></i><span>首页</span></div>
            <div class="item"><i></i><span>碎碎</span></div>
            <div class="item item-line"><i></i><span>管理</span></div>
            <div class="item"><i></i><span>赞</span></div>
            <div class="item"><i></i><span>踩</span></div>
            <div class="item item-line"><i></i><span>分享</span></div>
            <div class="item"><i></i><span>搜索</span></div>
            <div class="item"><i></i><span>顶部</span></div>
            <div class="item"><i></i><span>底部</span></div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "SideMenu",
        data(){
          return {
              show: false
          }
        },
        mounted () {
            this.$nextTick(() => {
                this.scroll()
                window.addEventListener('scroll', this.scroll)
            })
        },
        methods:{
            scroll(){
                let scrolltop = document.documentElement.scrollTop
                if(this.$isMobile){
                    scrolltop = document.body.scrollTop
                }
                let bodyHeight = window.screen.height - 500
                console.log(scrolltop,bodyHeight)
                if(scrolltop > bodyHeight){
                    this.show = true
                }else{
                    this.show = false
                }
            }
        }
    }
</script>

<style scoped lang="scss">
    .yu-sidemenu{
        box-shadow: 0 0 1px #b5b5b5;
        position: fixed;
        top: 50%;
        right: calc((100vw / 2) - 470px );
        border-radius: 10px;
        overflow: hidden;
        .items{
            .item{
                font-size: 0.12rem;
                padding: 0.05rem 0.1rem;
                cursor: pointer;
                &:hover{
                    background: #c28fef;
                    color: white;
                }
            }
            .item-line{
                border-bottom: solid 1px rgba(#b5b5b5,0.2);
                &:hover{
                    border-bottom: solid 1px rgba(#c28fef,0.2);
                }
            }
        }
    }
</style>