<template>
    <div class="yu-gaea" v-if="data">
        <div class="yu-gaea__header" v-if="data.title">
            <router-link to="/article">
                <h3>
                    {{data.title}}
                </h3>
            </router-link>
        </div>
        <div class="yu-gaea__content" v-if="data.content" :style="{
            '-webkit-line-clamp':`${limit}`
        }">
            {{data.content}}
        </div>
        <div class="yu-gaea__footer" v-if="footer">
            <span><i class="fa fa-user-circle-o"></i>谕℃ </span>
            <span><i class="fa fa-clock-o"></i>2019/11/22</span>
            <span><i class="fa fa-comments"></i>{{data.cmntNum == 0 ? '暂无评论' : data.cmntNum + '条评论'}}</span>
            <span><i class="fa fa-fire"></i>{{data.fire}}℃</span>
            <span><i class="fa fa-bookmark"></i>{{data.category ? data.category.name : '默认'}}</span>
            <span><i class="fa fa-tags"></i>{{data.tags ? tagHandler(data.tags) : ''}}</span>
        </div>
    </div>
</template>

<script>

    import RainBow from '../RainBow/index'

    export default {
        name: "Gaea",
        components:{
            RainBow
        },
        props:{
            data:{
                type: Object,
                default: null
            },
            footer:{
                type: Boolean,
                default: true,
            },
            limit:{
                type: String,
                default: "3",
            }
        },
        methods:{
            tagHandler(tags){
                let arr = []
                for(let tag of tags){
                    arr.push(tag.name)
                }
                if(arr.length > 0){
                    return arr.join(" / ")
                }
                return  ""
            }
        }
    }
</script>

